@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


body {
  background: #fff;
}
header {
  background-color: transparent;
  min-height: 3.5rem;
  padding: 0 10vw;
  z-index: 2;
}
.main-header {
  top: 20px;
  right: 0;
  left: 0;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Barlow Semi Condensed', sans-serif;
}
.brand-logo {
  display: flex;
  align-items: center;
  font-size: 5%;
  text-decoration: none;
  color: rgb(211, 209, 209);
  font-family: 'Barlow Semi Condensed';
  font-weight: bold;
}

.main-ul {
  display: flex;
  margin: 15px;
  padding: auto;
  list-style: none;
}

.main-nav a {
  font-size: x-large;
  font-family: 'Barlow Semi Condensed';
  color: #cfcfcf;
  text-decoration: none;
  padding: 0 1rem;
}
.main-nav a:hover {
  color: #ada8a8;
}
.toggle-button {
  display: none;
}

.submenuli{
  position: absolute;
  list-style-type: none;
  display:none;
  color: #afafaf;
  -webkit-animation: fadeIn 0.5s;
          animation: fadeIn 0.5s;
}


.submenuli a{
  font-size: medium;
}

.companymenuli:hover > ul{
  display:block;
}

.imgbox {
  position: relative;
}
.center-fit {
  object-fit: contain;
  max-width: 100%;
  max-height: 100vh;
  margin: auto;
}

.static {
  position:absolute;
  background: white;
}

.static:hover {
  opacity:0;
}


img.bg {
  /* Set rules to fill background */
  min-height: 100%;
  min-width: 1024px;
	
  /* Set up proportionate scaling */
  width: 100%;
  height: auto;
	
  /* Set up positioning */
  position: fixed;
  top: 0;
  left: 0;
}

.spn-style {
  overflow: hidden;
  text-align: center;
}

.spn-style:after {
  background-color: rgb(255, 255, 255);
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 10%;
}

.spn-style:after {
  left: 0.5em;
}

@media screen and (max-width: 1024px) { /* Specific to this particular image */
  img.bg {
    left: 50%;
    margin-left: -512px;   /* 50% */
  }
}

@media only screen and (max-width: 1000px) {
  header {
    padding: 0 5vw;
  }
}
@media only screen and (max-width: 800px) {

  .toggle-button {
    position: fixed;
    right: 20px;
    top: 0px;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    width: 60px;
    height: 5px;
    margin: 50px auto;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
  }

  .toggle-button span {
    display: block;
    position: absolute;
    height: 5px;
    width: 100%;
    background: #e9e9e9;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }
  .toggle-button span:nth-child(1) {
    top: 0px;
  }
  
  .toggle-button span:nth-child(2) {
    top: 18px;
  }
  
  .toggle-button span:nth-child(3) {
    top: 36px;
  }
  
  .toggle-button.open span:nth-child(1) {
    top: 18px;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
  .toggle-button.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
  .toggle-button.open span:nth-child(3) {
    top: 18px;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }


  .main-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .main-nav ul {
    flex-direction: column;
    align-items: flex-start;
    display: none;
  }
  .main-nav li {
    padding: 0.6rem 0;
  }
  .main-nav a {
    padding: 0;
  }
  .main-nav ul.active {
    visibility: visible;
    display: flex;
    -webkit-animation: fadeIn 0.5s;
            animation: fadeIn 0.5s;
  }

  .main-nav ul.hidden {
      display: flex;
      visibility: hidden;
      opacity: 0;
      transition: visibility 0s 0.25s, opacity 0.25s linear;
    }

  .submenuli{
    position: relative;
    list-style-type: none;
    display:none;
    color: #afafaf;
    -webkit-animation: fadeIn 0.5s;
            animation: fadeIn 0.5s;
  }

  .submenuli a{
    font-size: medium;
  }
  
  .companymenuli:hover > ul{
    display:block;
  }
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.elementToFadeInAndOut {
  opacity: 1;
  -webkit-animation: fade 8s linear;
          animation: fade 8s linear;
}


.frontImage {
  float: left;
  height: 100%;
  width: 100%;
  object-fit: cover;
} 


@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  20%{
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
} 


@keyframes fade {
  0% {
    opacity: 0;
  }
  20%{
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  70% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
